import React, { Component } from 'react';
import {request} from '../api/api.js';
import { Link } from "react-router-dom";
import { addFavorite,delFavorite } from '../api/favorite.js';
export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {
        articles: [],
      },
      rows:10,//每页显示的信息数
      totalPage:[],//总页数
      currentPage:1,//当前页
      favotitedisabled:false,//先默认一个false状态 都可以点击
    };
    this.fnFavorite = this.fnFavorite.bind(this)


  }
  //异步
  async fnFavorite(data){
    console.log(data);
    //异步
   await this.setState({
    favotitedisabled : true //点击的时候 把按钮的属性变为true 按钮变为不可点击
   })
    if(data.favorited){
       //取消点赞
      
    await  delFavorite(data).then((res)=>{
      console.log(res,'取消');
      
    })
     data.favorited = false  //点击取消点赞把按钮状态变为false 按钮可以点击
     data.favoritesCount += -1
     var key = this.state.data.articles.indexOf(data) //先获取我们点击的那个item的索引号  把索引号存给key
     this.state.data.articles[key] = data //data就是我们点击的那个item的信息集合 把我们点击的赋值一下 最后让页面刷新重新渲染
     //异步更新渲染页面
     await this.setState({
       articles:this.state.data.articles
     })
    }else{
      //点赞
      await  addFavorite(data).then((res)=>{
        console.log(res,'点赞');
        
      })
       data.favorited = true//点击点赞把按钮状态变为true 按钮不可以点击
       data.favoritesCount += 1
       var key = this.state.data.articles.indexOf(data) //先获取我们点击的那个item的索引号  把索引号存给key
       this.state.data.articles[key] = data //data就是我们点击的那个item的信息集合 把我们点击的赋值一下 最后让页面刷新重新渲染
          //异步更新渲染页面
       await this.setState({
         articles:this.state.data.articles
       })
    
    }
    await this.setState({
      favotitedisabled : false //点击的时候 把按钮的属性变为false 按钮变为可点击
     })
    
  }
  componentDidMount() {
    request({
      method: 'GET',
      url: '/api/articles',
      params: {
        limit:this.state.rows,//把我们设置的每页10条信息传给limit 传给后台
        
      }
    }).then((res) => {
      console.log(res);

      this.setState({
        data: res.data,
        totalPage: res.data.articlesCount % this.state.rows == 0 ? res.data.articlesCount / this.state.rows : res.data.articlesCount % this.state.rows +1
      })
     
    })

  }
  click=(index)=>{
    console.log(index,'index');
    this.setState({
      currentPage:index+1,//把当前页赋值给currentPage 索引从0开始 所以加一

    },function(){
      request({
        method: 'GET',
        url: '/api/articles',
        params: {
          limit:this.state.rows,//把我们设置的每页10条信息传给limit 传给后台
          offset:index * this.state.rows //偏移量 =（页数-1）* 每页消息显示数量  因为这里我们索引每有减一 所以不用页数减一了
        }
      }).then((res) => {
        console.log(res);
        this.setState({
          data: res.data,
        })
      })
    })
  }
  render() {
    return (
      <div>
        <div className="home-page">

          <div className="banner">
            <div className="container">
              <h1 className="logo-font">conduit</h1>
              <p>A place to share your knowledge.</p>
            </div>
          </div>

          <div className="container page">
            <div className="row">

              <div className="col-md-9">
                <div className="feed-toggle">
                  <ul className="nav nav-pills outline-active">
                    <li className="nav-item">
                      <a className="nav-link disabled" href="">Your Feed</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link active" href="">Global Feed</a>
                    </li>
                  </ul>
                </div>
                {
                  this.state.data.articles.map((item, index) => {
                    return (
                      <div className="article-preview" key={index}>
                        <div className="article-meta">
                          <a href="profile.html"><img src={item.author.image} /></a>
                          <div className="info">
                            <a href="" className="author">{item.author.username}</a>
                            <span className="date">{item.createdAt}</span>
                          </div>
                          <button className="btn btn-outline-primary btn-sm pull-xs-right"
                          onClick={()=>{
                            this.fnFavorite(item)
                          }} disabled={this.state.favotitedisabled}>
                            <i className="ion-heart"></i> {item.favoritesCount}
                          </button>
                        </div>
                        <a href="" className="preview-link">
                          <h1>{item.title}</h1>
                          <p>{item.description}</p>
                          <span>Read more...</span>
                        </a>
                      </div>
                    )
                  })
                }


              
              </div>

              <div className="col-md-3">
                <div className="sidebar">
                  <p>Popular Tags</p>

                  <div className="tag-list">
                    <a href="" className="tag-pill tag-default">programming</a>
                    <a href="" className="tag-pill tag-default">javascript</a>
                    <a href="" className="tag-pill tag-default">emberjs</a>
                    <a href="" className="tag-pill tag-default">angularjs</a>
                    <a href="" className="tag-pill tag-default">react</a>
                    <a href="" className="tag-pill tag-default">mean</a>
                    <a href="" className="tag-pill tag-default">node</a>
                    <a href="" className="tag-pill tag-default">rails</a>
                  </div>
                </div>
              </div>

              <ul className="pagination">
                {
                 Array.from({length:this.state.totalPage}).map((item,index) => {
                    return (
                      <li key={index} className={this.state.currentPage === index+1 ? 'page-item active' : 'page-item ' } >
                        <Link onClick={this.click.bind(null,index)} className="page-link" to='/'>{index+1}</Link>
                      </li>
                    )
                  })
                }
              </ul> 

            </div>
          </div>

        </div>
      </div>
    )
  }
}
