import { request } from './api';
import Cookie from 'js-cookie';

export let addFavorite = (item) => {
    console.log('添加点赞', '===========');
    var slug = item.slug;
    console.log(slug,'slug');
    //定义一个请求拦截
    request.interceptors.request.use( function (config){
        config.headers.Authorization = 'Token ' + Cookie.get('auth')
        // 请求发送之前 要做的事情
        return config;
    },function (error) {
        return Promise.reject(error);
    });
    //发送点赞的请求
    return request({
        method: 'POST',
        url: `/api/articles/${slug}/favorite`,
    })
}

//取消点赞
export let delFavorite = (item) => {
    console.log('取消点赞', '===========');
    var slug = item.slug;
    //定义一个请求拦截
    request.interceptors.request.use(function (config) {
        config.headers.Authorization = 'Token' + Cookie.get('auth')
        // 请求发送之前 要做的事情
        return config;
    }, function (error) {
        return Promise.reject(error);
    });

    //发送取消点赞的请求
    return request({
        method: 'DELETE',
        url: `/api/articles/${slug}/favorite`,
    })
}