import React, { Component } from 'react'
import { NavLink } from "react-router-dom"
import '../main.css'
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'

export default class Header extends Component {
  constructor(props){
   super(props)
   this.state = {
     auth : ''
   }
  }
  componentDidMount(){
    var that = this;
    // Pubsub.subscribe接收token值 传给data，data再重新赋值给auth
    Pubsub.subscribe('auth',function(msg,data){
      that.setState({
        auth:data
      })
    })
  }
  render() {
    //从Cookie中取出 如果有值就是登录过来的 没有就是没有登录
    if(Cookie.get('auth')){
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">conduit</a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
  
                  <NavLink className="nav-link" to="/home" activeClassName='active'>Home</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/editor" activeClassName='active'>
                    <i className="ion-compose"></i>&nbsp;&nbsp;New Post
              </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/settings" activeClassName='active'>
                    <i className="ion-gear-a"></i>&nbsp;Settings
              </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })" to="/profile" activeClassName='active'>
                    <img className="user-pic" src="https://z3.ax1x.com/2021/04/29/gkS4AI.jpg" ng-src="https://z3.ax1x.com/2021/04/29/gkS4AI.jpg" alt=""/>m
                    
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      )
    }else{
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">conduit</a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
  
                  <NavLink className="nav-link" to="/home" activeClassName='active'>Home</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/login" activeClassName='active'>Sign in</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/register" activeClassName='active'>Sign up</NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      )
    }
   
  }
}
