import React,{Component} from 'react'
import {BrowserRouter as Router,Route,Switch} from "react-router-dom"
import './main.css'
import Header from './Header'
import Home from './Home'
import Login from './Login'
import Profile from './Profile'
import Settings from './Settings'
import Article from './Article'
import Editor from './Editor'
import Footer from './Footer'
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'

 class App extends Component {
	 //不写的话刚登上号不会接收到token值 必须刷新一下才会切换路由 不刷新不会切换
	componentDidMount(){
		var that = this;
		// Pubsub.subscribe接收token值 传给data，data再重新赋值给auth
		Pubsub.subscribe('auth',function(msg,data){
		  that.setState({
			auth:data
		  })
		})
	  }
	render() {
		if(Cookie.get('auth')){
			return (
				<div>
					<Router>
					  <Header/>
					  <Footer/>
						<Switch>
							<Route exact path='/' component={Home}/> 
							<Route exact path='/home' component={Home}/> 
							<Route exact path='/editor' component={Editor}/> 
							<Route exact path='/register' component={Login}/> 
							<Route exact path='/login' component={Login}/> 
							<Route exact path='/article' component={Article}/> 
							<Route exact path='/settings' component={Settings}/> 
							<Route exact path='/profile' component={Profile}/> 
						</Switch>
					</Router>
					
				</div>
			)
		}else{
			return (
				<div>
					<Router>
					  <Header/>
					  <Footer/>
						<Switch>
							<Route exact path='/' component={Home}/> 
							<Route exact path='/home' component={Home}/> 
							<Route exact path='/editor' component={Home}/> 
							<Route exact path='/register' component={Login}/> 
							<Route exact path='/login' component={Login}/> 
							<Route exact path='/article' component={Home}/> 
							<Route exact path='/settings' component={Home}/> 
							<Route exact path='/profile' component={Home}/> 
						</Switch>
					</Router>
					
				</div>
			)
		}
		// return (
		// 	<div>
		// 		<Router>
		// 		  <Header/>
		// 		  <Footer/>
		// 			<Switch>
		// 			    <Route exact path='/' component={Home}/> 
		// 				<Route exact path='/home' component={Home}/> 
		// 				<Route exact path='/editor' component={Editor}/> 
		// 				<Route exact path='/register' component={Login}/> 
		// 				<Route exact path='/login' component={Login}/> 
		// 				<Route exact path='/article' component={Article}/> 
		// 				<Route exact path='/settings' component={Settings}/> 
		// 				<Route exact path='/profile' component={Profile}/> 
		// 			</Switch>
		// 		</Router>
				
		// 	</div>
		// )
	}
}
export default App;