import React, { Component } from 'react'
import {request} from '../api/api.js'
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'

class A extends React.Component{
  constructor(props){
    super(props)
    this.blur = this.blur.bind(this)
  }
  
  blur(event){
    var value = event.target.value;
    console.log(value);
    //把username值传给Login组件 发布者
    Pubsub.publish("value",value);
  }
  render(){
    return (
      <div>

           <fieldset className="form-group">
               <input className="form-control form-control-lg" type="text" placeholder="Your Name" onBlur={this.blur} />
            </fieldset>
      </div>
    )
  }
}

export default class Login extends Component {
  constructor(props){
    super(props)
    //为事件函数绑定this
    this.onsubmit = this.onsubmit.bind(this)
    this.onBlur1 = this.onBlur1.bind(this)
    this.onBlur2 = this.onBlur2.bind(this)
    //把表单中的input数据实现成响应式
    this.state={
      username:'',
      email:'',
      password:''
    }
  }
  componentDidMount(){

    // console.log(this.props);
    // request({
    //   method:'POST',
    //   url:'/api/users/login',
    //   data:{
    //     "email":'413401333@qq.com',
    //     "password":'myp123..'
    //   }
    // }).then(res=>console.log(res))
    Pubsub.subscribe('value',(msg,data)=>{
      this.setState({
        username:data
      })
    })
  }
  isLogin(){
    //判断是注册还是登录 
    return this.props.match.path === '/login'
  }
  onsubmit(event){
      event.preventDefault();
      // console.log('-----');
      //把获取到的输入的email和password值赋值给user
      let user ={
        username: this.state.username ? this.state.username : '',
        email: this.state.email,
        password:this.state.password
      }
      console.log(user,"user");
      request({
        method:'POST',
        url:this.state.username ? '/api/users' : '/api/users/login',
         //把获取到的输入的email和password值赋值给user  把新的user值赋值给data
        data:{
           user:user
        }
        //获取到执行then下的函数
      }).then(res => {
        //判断是否登录 set 放值  登录的时候获取token 然后把token的值给auth
        Cookie.set('auth',res.data.user.token);
        console.log(Cookie.get('auth'));
        console.log(res);
        //路由跳转 跳转到home
        this.props.history.push('/')
        // Pubsub先接收token值 然后发布出去   Pubsub.subscribe 接收发布出去的token值 
        Pubsub.publish('auth',res.data.user.token)
        
      })
  }
  //给input写一个失去焦点事件，失去焦点后就获取input框里输入的值
  onBlur1(event){
    console.log(event.target.value);
    this.setState({
      email : event.target.value
    })
  }
  
  onBlur2(event){
    console.log(event.target.value);
    this.setState({
      password : event.target.value
    })
  }
  render() {
    return (
      <div>
        <div className="auth-page">
          <div className="container page">
            <div className="row">

              <div className="col-md-6 offset-md-3 col-xs-12">
                <h1 className="text-xs-center">{this.isLogin()?'Sign in' : 'Sign up' }</h1>
                <p className="text-xs-center">
                  <a href="">{this.isLogin()?'Need an account' : 'Have an account' }</a>
                </p>

                <ul className="error-messages">
                  <li>That email is already taken</li>
                </ul>
                <form onSubmit={this.onsubmit}>
                   {this.isLogin()? '' : <A/>}
                  <fieldset className="form-group">
                    <input className="form-control form-control-lg" type="text" placeholder="Email" onBlur={this.onBlur1} />
                  </fieldset>
                  <fieldset className="form-group">
                    <input className="form-control form-control-lg" type="password" placeholder="Password" onBlur={this.onBlur2} />
                  </fieldset>
                  <button className="btn btn-lg btn-primary pull-xs-right">
                  {this.isLogin()?'Sign in' : 'Sign up' }
            </button>
                </form>

              </div>

            </div>
          </div>
        </div>
      </div>
    )
  }
}
